.footer {
  padding-top: 25px;
  padding-bottom: 14px;
  background-color: var(--dark-color);

  &__container {
    grid-template-columns: repeat(12, 1fr);
    gap: 0;

    @include for-desktop {
      gap: 0;
      grid-template-columns: var(--laptop-template-column);
    }

    @include big-tablet {
      grid-template-columns: var(--tablet-template-column);
    }
  }

  &__copyright {
    grid-column: 3 span;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &-descr {
      font-family: var(--sub-font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: var(--mercury-color);
    }

    @include for-desktop {
      grid-column: 3 span;
      margin-top: 6px;
    }

    @include big-tablet {
      grid-column: 3 span;
    }
  }

  &__logo {
    margin-bottom: 2px;
  }

  &__quote {
    grid-column: 5/4 span;
    padding-left: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--silver-color);

    &-text {
      max-width: 360px;
      margin: 0;

    }

    @include for-desktop {
      grid-column: 4/3 span;
      padding-left: 9px;
    }

    @include big-tablet {
      display: none;
    }
  }

  &__social {
    grid-column: 11/2 span;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    &-descr {
      display: inline-block;
      margin-bottom: 7px;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: var(--silver-color);

      @include big-tablet {
        margin-right: 0;
        margin-bottom: 8px;
      }
    }

    &-inner {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &-list {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
    }

    &-link {
      color: var(--silver-color);

      & svg path {
        fill: currentColor;
        transition: fill var(--transition-time-function);
      }

      &:focus-visible {
        outline: none;
        color: var(--tascany-color);
      }

      &:hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--grenadier-color);
      }
    }

    @include for-desktop {
      grid-column: 7/2 span;
    }

    @include big-tablet {
      grid-column: 3 span;
    }
  }

  @include for-desktop {
    padding-top: 24px;
  }
}
