.header {
  padding: 23px 0;
  padding-bottom: 22px;
  background-color: var(--dark-color);
  color: var(--light-color);

  @include for-desktop {
    padding-top: 26px;
    padding-bottom: 23px;
  }

  @include big-tablet {
    padding-bottom: 0;
  }

  &__container {
    grid-template-columns: var(--desktop-template-column);

    @include for-desktop {
      grid-template-columns: var(--laptop-template-column);
      gap: 0;
    }

    @include big-tablet {
      grid-template-columns: var(--tablet-template-column);
      padding-bottom: 23px;
    }

    @include mobile {
      grid-template-columns: var(--mobile-template-column);
    }
  }

  &__logo {
    align-self: center;
    grid-column: 1/2 span;
    display: inline-block;

    @include mobile {
      grid-column: 1;
    }
  }

  &__nav-wrapper {
    align-self: center;
    grid-column: 4/11;

    @include for-desktop {
      grid-column: 3/5 span;
      padding-left: 8px;
      margin-top: -3px;
      max-width: 94%;
    }

    @include big-tablet {
      grid-column: 12 span;
      order: 3;
    }
  }

  &__search-form {
    align-self: center;
    @include flex-v-center;
    justify-content: flex-end;
    grid-column: 12;

    @include for-desktop {
      grid-column: 8;
    }

    @include big-tablet {
      grid-column: 6;
      margin-top: -5px;
    }

    @include mobile {
      grid-column: 2;
    }
  }
}

.burger {
  position: relative;
  display: none;
  width: 20px;
  height: 18px;

  &__line {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--light-color);
    transform: translateY(-50%);

    &:nth-child(1) {
      top: 0;
    }
  }

  @include mobile {
    display: block;
  }
}
