// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --sub-font-family: "TTFirsNeue";
  --content-width: 1290px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // grid
  --grid-gap: 30px;
  --desktop-template-column: repeat(12, 1fr);
  --laptop-template-column: repeat(8, 1fr);
  --tablet-template-column: repeat(6, 1fr);
  --mobile-template-column: repeat(2, 1fr);


  // colors
  --light-color: #fff;
  --dark-color: #202020;
  --accent-color: #ff6e30;
  --tascany-color: #bc572b;
  --grenadier-color: #ce4307;
  --burnt-sienna-color: #f06666;
  --romantic-color: #ffd4c1;
  --red-orange-color: #ff3030;
  --mercury-color: #e9e9e9;
  --silver-color: #cacaca;
  --dusty-grey-color: #999999;
  --tundora-color: #4a4a4a;
  --light-dark-color: rgba(32, 32, 32, 0.85);

  // transition
  --transition-time-function: 0.3s ease-in-out;
}
