.search {
  position: relative;

  &.active .search-icon {
    opacity: 0;
  }

  &.active .close-icon {
    opacity: 1;
  }

  &.active &__input {
    transform: translateX(-30px) scaleX(1);
  }

  &__input {
    position: absolute;
    right: 0;
    width: 170px;
    padding: 2px 5px;
    border: none;
    border-bottom: 1px solid currentColor;
    background-color: var(--dark-color);
    font-family: var(--sub-font-family);
    font-size: 14px;
    line-height: 18px;
    color: var(--mercury-color);
    z-index: 10;
    transform: translateX(-30px) scaleX(0);
    transform-origin: right;
    transition: transform var(--transition-time-function), border-color var(--transition-time-function);

    &:focus-visible {
      outline: none;
      border-color: var(--accent-color);
    }

    &:hover {
      border-color: var(--accent-color);
    }

    &:active {
      border-color: var(--grenadier-color);
    }

    @include for-desktop {
      width: 210px;
    }

    @include mobile {
      width: calc(100% *2 - 20px);
    }
  }

  &__btn {
    position: relative;
    width: 25px;
    height: 25px;
    color: var(--light-color);
    transition: color var(--transition-time-function);

    &:focus-visible {
      outline: none;
      color: var(--accent-color);
    }

    &:hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--grenadier-color);
    }

    @include mobile {
      width: 20px;
      height: 20px;
    }
  }

  &__icon {
    position: absolute;
    inset: 0;
    transition: opacity var(--transition-time-function);

    & svg {
      fill: currentColor;
    }
  }
}

.search-icon svg{
  display: inline-block;
  width: 100%;
  height: 100%;

}

.close-icon {
  @include flex-all-center;
  border-radius: 50%;
  background-color: var(--dusty-grey-color);
  opacity: 0;
}
