.phone-link {
  position: relative;
  display: inline-flex;
  justify-content: flex-start;
  max-width: fit-content;
  font-family: var(--sub-font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: var(--accent-color);
  transition: color var(--transition-time-function);

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background-color: var(--tascany-color);
    transform: scaleX(0);
    opacity: 0;
    transition: opacity var(--transition-time-function), transform var(--transition-time-function);
  }

  &:focus-visible {
    outline: none;
    color: var(--light-color);
  }

  &:focus-visible::before {
    transform: scaleX(1);
    opacity: 1;
    z-index: -1;
  }

  &:hover {
    color: var(--tascany-color);
  }

  &:active {
    color: var(--grenadier-color);
  }

  &__text {
    position: relative;
    z-index: 10;
  }

  &__icon {
    margin-right: 4px;
  }

  &__icon-svg path {
    fill: currentColor;
  }

}
