.projects {
  &__title {
    margin-bottom: 12px;
  }

  &__list {
    grid-template-columns: var(--desktop-template-column);

    @include for-desktop {
      grid-template-columns: var(--laptop-template-column);
      row-gap: 24px;
    }

    @include small-tablet {
      grid-template-columns: var(--tablet-template-column);
      row-gap: 0;
    }
  }
}

.project {
  position: relative;
  grid-column: 2 span;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  min-height: 300px;
  background-repeat: no-repeat;
  background-position: top center;

  &__descr {
    display: -webkit-box;
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--dark-color);
    overflow: hidden;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    @include for-desktop {
      margin-bottom: 17px;
      -webkit-line-clamp: 2;
    }

    @include small-tablet {
      margin-bottom: 10px;
      -webkit-line-clamp: 3;
    }
  }

  &__date {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    color: var(--dusty-grey-color);

    &-icon {
      margin-right: 5px;

      & path {
        fill: currentColor;
      }
    }
  }

  &__mark {
    top: 6px;
    right: 7px;
  }

  &1 {
    background-image: url(../img/projects-small1.jpg);

    @include for-desktop {
      background-image: url(../img/projects-small1-1024.jpg);
    }
  }

  &2 {
    background-image: url(../img/projects-small2.jpg);

    @include for-desktop {
      background-image: url(../img/projects-small2-1024.jpg);
    }
  }

  &3 {
    background-image: url(../img/projects-small3.jpg);

    @include for-desktop {
      grid-column: 4 span;
      background-image: url(../img/projects-small3-1024.jpg);
    }

    @include small-tablet {
      grid-column: 2 span;
      background-image: url(../img/projects-small3-768.jpg);

      & .project__descr {
        margin-bottom: 32px;
        -webkit-line-clamp: 2;
      }
    }
  }

  &4 {
    background-image: url(../img/projects-big1.jpg);

    @include for-desktop {
      background-image: url(../img/projects-big1-1024.jpg);
    }

    @include small-tablet {
      background-image: url(../img/projects-big1-768.jpg);
    }

    @include small-tablet {
      margin-bottom: 26px;
    }
  }

  &5 {
    background-image: url(../img/projects-big2.jpg);

    @include for-desktop {
      background-image: url(../img/projects-big2-1024.jpg);
    }

    @include big-tablet {
      background-image: url(../img/projects-big2-768.jpg);
    }


  }

  &6 {
    background-image: url(../img/projects-middle1.jpg);

    @include for-desktop {
      background-image: url(../img/projects-middle1-1024.jpg);
    }
  }

  &7 {
    background-image: url(../img/projects-middle2.jpg);

    @include for-desktop {
      background-image: url(../img/projects-middle2-1024.jpg);
    }
  }

  @include for-desktop {
    min-height: 285px;

    &:nth-child(4) {
      padding-bottom: 37px;
    }

    &:nth-child(5) {
      order: 7;
    }
  }

  @include small-tablet {
    min-height: 300px;

    &:nth-child(4) {
      padding-top: 34px;
      padding-bottom: 32px;
    }

    &:nth-child(5) {
      padding-top: 34px;
      padding-bottom: 32px;
    }
  }
}

.big-project {
  grid-column: 6 span;
  padding: 30px;
  padding-top: 21px;
  background-position: center;
  background-size: cover;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 293px;
    height: 100%;
  }

  &__date {
    margin-bottom: 2px;
    color: var(--light-color);

    @include for-desktop {
      margin-bottom: 2px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 6px;
    font-family: var(--sub-font-family);
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: var(--light-color);

    @include for-desktop {
      margin-bottom: 6px;
    }
  }

  &__descr {
    color: var(--light-color);
  }

  &__btn {
    margin-top: auto;
  }

  @include for-desktop {
    grid-column: 5 span;
    min-height: 307px;
    padding-top: 30px;

    & .project__descr {
      -webkit-line-clamp: 3;
    }
  }

  @include small-tablet {
    grid-column: 6 span;
    margin-top: 21px;
    background-size: contain;

    & .project__mark {
      top: 10px;
    }
  }
}

.middle-project {
  grid-column: 3 span;

  @include for-desktop {
    grid-column: 3 span;
    min-height: 307px;

    & .project__descr {
      -webkit-line-clamp: 3;
    }
  }

  @include small-tablet {
    & .project__descr {
      margin-bottom: 15px;
    }
  }
}
