.fill-btn {
  position: relative;
  display: inline-block;
  padding: 15px 22px;
  background-color: var(--accent-color);
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: var(--light-color);z-index: 10;
  transition: background-color var(--transition-time-function);

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    border: 3px solid var(--grenadier-color);
    opacity: 0;
    transition: opacity var(--transition-time-function);
  }

  &:focus-visible:not(:hover):not(:active) {
    outline: none;

    &::before {
      opacity: 1;
    }
  }

  &:hover {
    outline: none;
    background-color: var(--tascany-color);
  }

  &:active {
    outline: none;
    background-color: var(--grenadier-color);
  }
}
