.hero {
  padding-top: 41px;
  padding-bottom: 68px;
  background-color: var(--light-color);

  &__container {
    grid-template-columns: var(--desktop-template-column);

    @include for-desktop {
      grid-template-columns: var(--laptop-template-column);
    }

    @include small-tablet {
      grid-template-columns: var(--tablet-template-column);
    }
  }

  &__content-highlight {
    grid-column: 6 span;

    @include for-desktop {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 3 span;
    }
  }

  &__content-grid {
    grid-column: 6 span;

    &__title {
      grid-column: 2 span;
      margin: 0;
      margin-bottom: 19px;
      font-family: var(--sub-font-family);
      font-weight: 700;
      font-size: 24px;
      line-height: 104%;
      color: #202020;

      @include for-desktop {
        margin-bottom: 25px;
      }

      @include small-tablet {
        margin-bottom: 18px;
        font-size: 22px;
      }
    }

    @include for-desktop {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 3 span;
    }
  }

  @include for-desktop {
    padding-top: 34px;
    padding-bottom: 67px;
  }
}

.highlight {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 60px;
  padding-right: 0;
  background-image: url(../img/hero-Main.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__title {
    max-width: 450px;
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--sub-font-family);
    font-weight: 700;
    font-size: 36px;
    line-height: 104%;
    color: var(--light-color);

    &--descr {
      display: block;
      margin-bottom: 2px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      @include for-desktop {
        margin-bottom: 8px;
      }

      @include small-tablet {
        margin-bottom: 14px;
      }
    }

    @include for-desktop {
      margin-bottom: 24px;
      font-size: 34px;
      line-height: 100%;
    }

    @include small-tablet {
      line-height: 90%;
    }
  }

  &__mark {
    top: 15px;
    right: 15px;
  }

  @include for-desktop {
    padding: 30px;
    background-image: url(../img/hero-Main1024.jpg);
  }

  @include small-tablet {
    background-image: url(../img/hero-Main-768.jpg);
  }
}

.hero-works {
  grid-template-columns: var(--mobile-template-column);

  &__card {
    font-size: 0;
  }

  &__major {
    grid-column: 2 span;
  }

  &__minor {
    grid-column: 1 span;
  }
}
