.about {
  padding-top: 30px;
  padding-bottom: 70px;
  background-color: var(--dark-color);
  color: var(--light-color);

  &__wrapper {
    grid-template-columns: var(--desktop-template-column);

    @include for-desktop {
      grid-template-columns: var(--laptop-template-column);
    }
  }

  &__content {
    grid-column: 6 span;
    padding-bottom: 2px;

    @include for-desktop {
      grid-column: 4 span;
    }
  }

  &__title {
    grid-column: 12 span;
    margin-bottom: 3px;

    @include for-desktop {
      margin-bottom: 2px;
    }
  }

  &__descr {
    max-width: 625px;
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    color: var(--silver-color);

    @include for-desktop {
      max-width: 465px;
      padding-top: 3px;
      padding-bottom: 5px;
    }
  }

  &__subscribe {
    grid-column: 6 span;

    @include for-desktop {
      grid-column: 4 span;
    }
  }

  @include for-desktop {
    padding-bottom: 81px;
  }

  @include small-tablet {
    padding-bottom: 56px;
  }
}

.subscribe {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  height: auto;
  background-image: url(../img/about-bg.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;

  &__title {
    margin: 0;
    margin-bottom: 18px;
    font-family: var(--sub-font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 22px;
  }

  &__form {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    @include for-desktop {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__input {
    width: 100%;
    margin-right: 30px;
    padding: 14px 23px;
    border: 1px solid transparent;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--light-color);
    background-color: var(--tundora-color);
    transition: border-color var(--transition-time-function);


    &:focus-visible {
      outline: none;
      border-color: var(--accent-color);

    }

    &:hover::placeholder {
      color: var(--accent-color);
    }

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--silver-color);
      transition: color var(--transition-time-function);
    }

    &.just-validate-error-field {
      border-color: var(--burnt-sienna-color);
    }

    @include for-desktop {
      width: 342px;
      margin-right: 0;
      margin-bottom: 21px;
    }
  }

  @include for-desktop {
    background-image: url(../img/about-bg-1024.jpg);
  }

  @include big-tablet {
    padding-bottom: 14px;
    background-image: url(../img/about-bg-768.jpg);
    background-size: 100% 70%;
  }

  @include small-tablet {
    background-size: contain;
  }
}
