.close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: var(--dusty-grey-color);
  color: var(--light-color);
  transition: color var(--transition-time-function);

  &:focus-visible {
    outline: none;
    color: var(--accent-color);
  }

  &:hover {
    color: var(--accent-color);
  }

  &:active {
    color: var(--grenadier-color);
  }

  &__icon {
    fill: currentColor;
  }
}
