.nav {
  &__close-btn {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__list {
    @include flex-all-sb;

    @include for-desktop {
      justify-content: space-between;
    }
  }

  &__item:not(:last-child) {
    margin-right: 78px;

    @include big-desktop {
      margin-right: 33px;
    }

    @include for-desktop{
      margin: 0;
    }
  }

  &__link {
    position: relative;
    display: block;
    font-family: var(--sub-font-family);
    font-size: 16px;
    line-height: 21px;
    color: currentColor;
    transition: color var(--transition-time-function);

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      background-color: var(--tascany-color);
      transform: scaleX(0);
      opacity: 0;
      transition: opacity var(--transition-time-function), transform var(--transition-time-function);
    }

    &:focus-visible {
      outline: none;
    }

    &:focus-visible:not(:hover)::before {
      transform: scaleX(1);
      opacity: 1;
    }

    &:hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--grenadier-color);
    }

  }

  &__text {
    position: relative;
    z-index: 10;
  }

  &__phone-link {
    display: none;

    @include mobile {
      display: block;
    }
  }
}


