.contacts {
  padding-bottom: 68px;

  &__title {
    margin-bottom: 18px;
  }

  &__content {
    grid-template-columns: repeat(12, 1fr);

    @include for-desktop {
      grid-template-columns: var(--laptop-template-column);
    }

    @include big-tablet {
      grid-template-columns: var(--tablet-template-column);
      row-gap: 28px;
    }
  }

  &__map {
    position: relative;
    grid-column: 8 span;
    padding-top: 4px;
    overflow: hidden;

    @include for-desktop {
      grid-column: 5 span;
    }

    @include big-tablet {
      grid-column: 6 span;
    }
  }

  &__info {
    position: absolute;
    top: 4px;
    left: 0;
    z-index: 100;
    transform-origin: left;
    transition: transform var(--transition-time-function), opacity var(--transition-time-function);

    &.hide {
      opacity: 0;
      transform: scaleX(0);
    }

    @include for-desktop {
      top: initial;
      bottom: 0;
      width: 100%;
      height: auto;
    }
  }

  @include big-tablet {
    padding-top: 65px;
    padding-bottom: 68px;
  }
}

.address-info {
  padding-top: 175px;
  padding-left: 60px;

  width: 410px;
  height: 100%;
  background-color: var(--light-dark-color);

  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-family: var(--sub-font-family);
    font-weight: 400;
    font-style: normal;
    font-size: 24px;
    line-height: 22px;
    color: var(--light-color);
  }

  &__location {
    position: relative;
    margin-bottom: 2px;
    max-width: 275px;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 28px;
    color: var(--silver-color);

    @include big-tablet {
      max-width: 440px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  @include for-desktop {
    width: 100%;
    height: auto;
    padding: 25px 30px;
    padding-bottom: 20px;
  }

  @include big-tablet {
    padding: 24px 30px;
  }
}

.laptop-link {
  position: absolute;
  bottom: 0;
  right: -15%;
  display: none;

  @include for-desktop {
    display: flex;
  }

  @include big-tablet {
    display: none;
  }
}

.desktop-link {
  @include for-desktop {
    display: none;
  }

  @include big-tablet {
    display: flex;
  }
}

.map__wrapper {
  width: 100%;
  height: 460px;
}

[class*="ground-pane"] {
  filter: grayscale(1);
}

[class*="copyrights-pane"] {
  display: none;
}

.contacts__feedback {
  grid-column: 4 span;

  @include for-desktop {
    grid-column: 3 span;
  }

  @include big-tablet {
    grid-column: 4 span;
  }
}

.feedback {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &__title {
    margin: 0;
    margin-top: -2px;
    margin-bottom: 4px;
    font-family: var(--sub-font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 22px;
    color: var(--dark-color);

    @include for-desktop{
      margin-top: -4px;
      margin-bottom: 6px;
    }
  }

  &__descr {
    max-width: 390px;
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--dusty-grey-color);

    @include big-tablet {
      margin-bottom: 12px;
      max-width: none;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 18px;
    width: 100%;
  }

  &__label {
    position: relative;
    width: 100%;
  }

  &__input {
    width: 100%;
    padding: 15px 22px;
    padding-bottom: 16px;
    border: 1px solid transparent;
    background-color: var(--mercury-color);
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--dark-color);
    transition: border-color var(--transition-time-function);

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--dusty-grey-color);
      transition: color var(--transition-time-function);
    }

    &:focus-visible:not(:hover) {
      outline: none;
      border-color: var(--accent-color);
    }

    &:hover {
      outline: none;
      &::placeholder {
        color: var(--accent-color);
      }
    }

    @include for-desktop{
      padding-bottom: 15px;
    }
  }

  &__comment {
    max-height: 150px;
    margin-bottom: 2px;
    resize: none;
  }

  &__btn {
    @include small-tablet {
      margin-top: 4px;
    }
  }
}
