/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: (1050px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin big-tablet {
  @media (max-width: (850px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (780px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (620px)) {
    @content;
  }
}
