.stroke-btn {
  display: inline-block;
  padding: 13px 20px;
  border: 3px solid var(--accent-color);
  background-color: transparent;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: var(--light-color);
  transition: border-color var(--transition-time-function), background-color var(--transition-time-function);

  &:focus-visible {
    outline: none;
    background-color: var(--tascany-color);
  }

  &:hover {
    background-color: var(--accent-color);
  }

  &:active {
    border-color: var(--grenadier-color);
    background-color: var(--grenadier-color);
  }
}
